import { ParallaxBanner } from "react-scroll-parallax";
import { Typography, Box } from "@mui/material";

const Landing = ({ text, image, layout }) => {
  const background = {
    children: (
      <Box
        height={layout ? "100%" : "100vh"}
        sx={{
          backgroundImage: `url(${image})`,
          backgroundSize: "cover",
          backgroundPosition: layout ? "center" : "60% 75%",
          backgroundRepeat: "no-repeat",
        }}
      ></Box>
    ),
    translateY: [0, 50],
    opacity: [0.5, 1],
    scale: [3, 1, "easeOutCubic"],
  };

  const headline = {
    translateY: [0, 10],
    opacity: [1, 0.2],
    scale: [0, 2, "easeOutCubic"],
    children: (
      <Box
        display="flex"
        alignItems="center"
        height="100%"
        justifyContent="center"
        sx={{ backgroundBlendMode: "screen" }}
      >
        <Typography variant="h1" sx={{ fontSize: 48 }} color="neutral.main">
          {text}
        </Typography>
      </Box>
    ),
  };

  return (
    <ParallaxBanner
      layers={[background, headline]}
      style={{ height: "100vh" }}
    />
  );
};

export default Landing;
